import { Pipe, PipeTransform } from '@angular/core';

/*
 * example:
 * let someArray = [{id: 1, name: 'a'}, {id: 2, name: 'b'}] 
 * someArray | filterOne:'id':1
 * will return {id: 1, name: 'a'}
 */

@Pipe({
  name: 'filterOne'
})
export class FilterOnePipe implements PipeTransform {

  transform(arr: any, prop: string, value: any): any {
    let result = null;
    if (arr instanceof Array) {
      result = (<Array<any>>arr).filter(item => item[prop] === value)[0]
    }
    return result;
  }

}



